<template>
  <div class="container mt-4">
    <div class="row justify-content-center">
      <div class="col-md-8">
        <h1 class="font-weight-light text-center">Attendees</h1>
        <div class="card bg-light mb-4">
          <div class="card-body text-center">
            <div class="input-group input-group-lg">
              <input
                type="text"
                placeholder="Search Attendees"
                class="form-control"
                v-model="searchQuery"
                ref="searchQuery"
              />
              <div
                class="input-group-append"
                v-if="user !== null && user.uid == userID"
              >
                <button
                  class="btn btn-sm btn-outline-info"
                  title="Pick a random attendee"
                  @click="chooseRandom"
                >
                  <font-awesome-icon icon="random"></font-awesome-icon>
                </button>
                <button
                  class="btn btn-sm btn-outline-info"
                  title="Reset Search"
                  @click="resetQuery"
                >
                  <font-awesome-icon icon="undo"></font-awesome-icon>
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="row justify-content-center">
      <div
        class="col-8 col-sm-6 col-md-4 col-lg-3 mb-2 p-0 px-1"
        v-for="attendee in filteredAttendees"
        :key="attendee.id"
      >
        <div class="card">
          <div
            class="card-body px-3 py-2 d-flex align-items-center justify-content-center"
          >
            <div
              class="btn-group pr-2"
              v-if="user !== null && user.uid === userID"
            >
              <button
                class="btn btn-sm"
                :class="[
                  attendee.star ? 'text-success' : '',
                  'btn-outline-secondary'
                ]"
                title="Give a Star"
                @click="toggleStar(attendee.id)"
              >
                <font-awesome-icon icon="star"></font-awesome-icon>
              </button>
              <a
                class="btn btn-sm btn-outline-secondary"
                title="Send email"
                :href="'mailto:' + attendee.email"
              >
                <font-awesome-icon icon="envelope"></font-awesome-icon>
              </a>
              <button
                class="btn btn-sm btn-outline-secondary mr-3"
                title="Delete Attendee"
                @click="deleteAttendee(attendee.id)"
              >
                <font-awesome-icon icon="trash"></font-awesome-icon>
              </button>
            </div>
            <div>{{ attendee.displayName }}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import db from "../db.js";
import { FontAwesomeIcon } from "@fortawesome/vue-fontawesome";
export default {
  name: "Attendees",
  data: function() {
    return {
      attendees: [],
      displayAttendees: [],
      searchQuery: "",
      userID: this.$route.params.userID,
      meetingID: this.$route.params.meetingID
    };
  },
  props: ["user"],
  components: {
    FontAwesomeIcon
  },
  computed: {
    filteredAttendees: function() {
      // create a filter that you will run through an array to filter it based on the predefined rules
      const filterData = item =>
        item.displayName.toLowerCase().match(this.searchQuery.toLowerCase()) &&
        true;
      //filter the array and return
      return this.displayAttendees.filter(filterData);
    }
  },
  mounted() {
    // check for any addition to the attendees collection and bring in the updated/latest data
    db.collection("users")
      .doc(this.userID)
      .collection("meetings")
      .doc(this.meetingID)
      .collection("attendees")
      .onSnapshot(snapshot => {
        const buffer = [];
        snapshot.forEach(doc => {
          buffer.push({
            id: doc.id,
            displayName: doc.data().displayName,
            email: doc.data().email,
            star: doc.data().star
          });
        });
        this.attendees = buffer;
        this.displayAttendees = this.attendees;
      });
  },
  methods: {
    chooseRandom: function() {
      //choose a random attendee
      const randomAttendee = Math.floor(Math.random() * this.attendees.length);
      this.displayAttendees = [this.attendees[randomAttendee]];
    },
    resetQuery() {
      this.displayAttendees = this.attendees;
      this.searchQuery = "";
      this.$refs.searchQuery.focus();
    },
    toggleStar: function(payLoad) {
      if (this.user.uid !== null && this.user.uid == this.userID) {
        //to update a specific document in a collection
        //create a variable/const and store a reference to the path to get to the document on the variable 
        const ref = db
          .collection("users")
          .doc(this.userID)
          .collection("meetings")
          .doc(this.meetingID)
          .collection("attendees")
          .doc(payLoad); //ref now stores the path to get to the particular document

        ref.get().then(doc => {
          const star = doc.data().star;
          if (star) {
            ref.update({
              star: !star
            });
          } else {
            ref.update({
              star: true
            });
          }
        });
      }
    },
    deleteAttendee: function(payLoad) {
      if (this.user.uid !== null && this.user.uid == this.userID) {
        db.collection("users")
          .doc(this.userID)
          .collection("meetings")
          .doc(this.meetingID)
          .collection("attendees")
          .doc(payLoad)
          .delete();
      }
    }
  }
};
</script>
