<template>
  <div id="app">
    <Navigation :user="user" @logout="logout" />
    <router-view
      class="container"
      :user="user"
      :error="error"
      :meetings="meetings"
      @logout="logout"
      @addMeeting="addMeeting"
      @checkIn="checkIn"
      @deleteMeeting="deleteMeeting"
    />
  </div>
</template>
<script>
import Navigation from "@/components/Navigation.vue";
import db from "./db.js";
import Firebase from "firebase";

export default {
  name: "app",
  data: function() {
    return {
      user: null,
      meetings: [],
      error: null
    };
  },
  components: {
    Navigation
  },
  methods: {
    logout: function() {
      Firebase.auth()
        .signOut()
        .then(() => {
          this.user = null;
          this.$router.push("login");
        });
    },
    addMeeting: function(payLoad) {
      // how to add a new document/record to a specific document
      db.collection("users")
        .doc(this.user.uid)
        .collection("meetings")
        .add({
          name: payLoad,
          createdAt: Firebase.firestore.FieldValue.serverTimestamp()
        });
    },
    deleteMeeting: function(payLoad) {
      //deleting a document from a collection, we must know the ID of the document
      db.collection("users")
        .doc(this.user.uid)
        .collection("meetings")
        .doc(payLoad)
        .delete();
    },
    checkIn: function(payLoad) {
      //we have to determine if the meeting exists by passing the meetingID
      // before we proceed to checkin (create a new record) a new attendee
      db.collection("users")
        .doc(payLoad.userID)
        .collection("meetings")
        .doc(payLoad.meetingID)
        .get()
        .then(doc => {
          if (doc.exists) {
            db.collection("users")
              .doc(payLoad.userID)
              .collection("meetings")
              .doc(payLoad.meetingID)
              .collection("attendees")
              .add({
                displayName: payLoad.displayName,
                email: payLoad.email,
                checkedInTime: Firebase.firestore.FieldValue.serverTimestamp()
              })
              .then(() =>
                this.$router.push(
                  "/attendees/" + payLoad.userID + "/" + payLoad.meetingID
                )
              );
          } else {
            this.error = "Sorry no such meeting exists";
          }
        });
    }
  },
  mounted() {
    Firebase.auth().onAuthStateChanged(user => {
      if (user) {
        this.user = user;

        //queries the collection meetings created under a specific user id created under the users collection
        //the query is not a one time query but a continous query for any changes to the collection
        db.collection("users")
          .doc(this.user.uid)
          .collection("meetings")
          .onSnapshot(snapshot => {
            const buffer = [];
            snapshot.forEach(doc => {
              buffer.push({
                id: doc.id,
                name: doc.data().name
              });
            });
            //sort the array using javascript's sort function
            this.meetings = buffer.sort((a, b) => {
              if (a.name.toLowerCase() < b.name.toLowerCase()) {
                return -1;
              } else {
                return 1;
              }
            });
          });
      }
    });
    // queries the firestore database(users collection) for a document with a specific ID and retrives the name field
    // db.collection("users")
    //   .doc("g30OfNv1dSZatgU9atXH")
    //   .get()
    //   .then(snapshot => {
    //     this.name = snapshot.data().name;
    //   });
  }
};
</script>
